<template>
    <div class="register-box">
       <div class="register-header-box">
            <div class="register-header">
            <a href="#" class="register-logo"></a>
            <div class="nav-login-box">
                <span>已有美团账号?</span>
                <router-link :to="{name:'landing'}" class="login-input">登录</router-link>
            </div>
            </div>
       </div>
        <div class="register-content">
            <form action="#">
                <div class="form-mobile">
                    <label>手机号</label>
                    <input type="text" name="mobile" id="" @focus="mobileFocus" 
                           @blur="mobileBlur" v-model="mobileValue">
                    <span v-if="mobileInput == 1">注册成功后,全美团通用</span>
                     <span v-if="mobileInput == 2" style="color:red">
                               <i class="el-icon-error" ></i>
                               请输入正确的11位手机号码
                           </span>
                </div>
                <div class="verify-wrapper">
                    <input type="text" :value="message" v-if="!isStartCountDown" @click.stop="startCountDown"
                           >
                    <input type="text" name="countDown" id="" :value="'重新获取('+countDown+')'" disabled
                           v-else >
                    <span v-if="isStartCountDown">已发送，1分钟后可重新获取。</span>   
                </div>
                <div class="form-sms">
                    <label>短信动态码</label>
                    <input type="text" name="sms" id="" @blur="smsBlur">
                    <span v-if="smsError" style="color:red">
                       <i class="el-icon-error" ></i>
                        请输入短信动态码
                    </span>
                </div>
                <div class="form-pw">
                    <label>创建密码</label>
                    <input type="text" name="pw" id="">
                    <div class="pw-strength">
                        <span :class="{'getPw':passWordSafe == 1}">弱</span>
                        <span :class="{'getPw':passWordSafe == 2}">中</span>
                        <span :class="{'getPw':passWordSafe == 3}">强</span>
                    </div>
                </div>
                <div class="form-pw2">
                    <label>确认密码</label>
                    <input type="text" name="" id="">
                </div>
                <div class="form-field">
                    <input type="submit" name="" id="" value="同意以下协议并注册">
                </div>
                <p class="term">
                    <a href="#">《美团网用户协议》</a>
                </p>
            </form>
        </div>
        <div class="register-footer">
             <p class="copyright">
                ©
                <a href="#">meituan.com</a>
                <a href="#">京ICP证070791号</a>
                京公网安备11010502025545号
            </p>
        </div>
    </div>
</template>

<script>
export default {
  data(){
      return {
          passWordSafe:0,
          mobileInput:1,
          mobileValue:"",
          countDown:60,
          isStartCountDown:false,
          message:'免费获取验证码',
          smsError:false
          
      }
  },
  methods:{
      smsBlur(){
          this.smsError = true;
      },
      mobileFocus(){
          this.mobileInput = 0;
      },
      mobileBlur(){
          if(this.mobileValue == ''){
              this.mobileInput = 2;

          }
      },
      startCountDown(){
          if(this.mobileValue.length !== 11){
              this.mobileInput = 2;
              return false;
          }
          this.isStartCountDown = true;
           var setTimer = null;
           const that = this;
          const myCountDown = function(){
            
             setTimer = setTimeout(()=>{
              that.countDown--;
              if(that.countDown == 58){
                  that.isStartCountDown = false;
                  that.countDown = 60;
                  that.message = '重新获取';
                  clearTimeout(setTimer);
                  return false;
                  
              };
              myCountDown();
          },1000)
          };
          myCountDown();
      }
  }
}
</script>

<style lang="less">
  @import url('../../assets/register/register.less');
</style>



